body {
  box-sizing: border-box;
  color: #000;
  background-color: #fff;
  font-family: DM Sans, sans-serif;
  font-size: 16px;
}

body.lock {
  overflow: hidden;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  outline: none;
  text-decoration: none;
  transition-duration: .25s;
  transition-timing-function: ease;
  display: inline-block;
}

button {
  box-shadow: 0px;
  color: inherit;
  fill: currentColor;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  outline: 0;
  margin: 0;
  padding: 0;
  font-family: inherit;
  transition-duration: .25s;
  transition-timing-function: ease;
  display: inline-block;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

address {
  font-style: normal;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
